.hide-text {
    text-indent: 100%;
    white-space: nowrap;
    overflow: hidden;
    display: block;
    width: 0;
    height: 0
}

.clearfix:after {
    content: "";
    display: table;
    clear: both
}

.hidden {
    display: none
}

html {
    background-color: #ffffff
}

body {
    font-family: 'Open Sans', sans-serif;
    font-size: 14px;
    background-color: #ffffff
}

.side-padding-5 {
    padding: 0 5%
}

.flex-container {
    display: -ms-flexbox;
    display: flex
}

.flex-container.f-wrap {
    -ms-flex-wrap: wrap;
    flex-wrap: wrap
}

.flex-space-btw {
    -ms-flex-pack: justify;
    justify-content: space-between
}

.button {
    border: 1px solid rgba(0, 0, 0, 0.1);
    padding: 0.6em 1em 0.7em 1em;
    border-radius: 20px;
    text-decoration: none;
    color: rgba(0, 0, 0, 0.6);
    font-size: 14px;
    outline: none;
    -webkit-transition: all .1s;
    -o-transition: all .1s;
    transition: all .1s
}

.button:hover {
    text-decoration: none;
    color: rgba(0, 0, 0, 0.8);
    border-color: rgba(0, 0, 0, 0.3)
}

.button.green-border-button {
    color: #00ab6b !important;
    border-color: #00ab6b;
    background-color: #fff
}

.button.green-inner-button {
    color: #fff;
    background-color: #02b875;
    border-color: #02b875
}

.button.green-inner-button:hover {
    color: #02b875;
    background-color: #ffffff;
    border-color: #02b875
}

.count-button-wrapper {
    position: relative;
    width: 35px;
    height: 35px;
    border: 1px solid rgba(0, 0, 0, 0.1);
    border-radius: 50%;
    background-color: #fff;
    margin-right: 20px
}

.count-button-wrapper .count-button {
    position: absolute;
    top: 50%;
    left: 50%;
    -webkit-transform: translate(-50%, -50%);
    -ms-transform: translate(-50%, -50%);
        transform: translate(-50%, -50%);
    font-size: 12px;
    font-weight: bold
}

.unfollow-button {
    position: relative;
    width: 6.5em;
    overflow: hidden;
    display: inline-block
}

.unfollow-button .content {
    position: absolute;
    padding: 0.5em 1em;
    left: 0;
    -webkit-transition: top 0.4s ease 0.2s;
    -o-transition: top 0.4s ease 0.2s;
    transition: top 0.4s ease 0.2s;
    overflow: hidden
}

.unfollow-button .top {
    top: 0
}

.unfollow-button:hover .top {
    top: -80px
}

.unfollow-button .bottom {
    top: 80px
}

.unfollow-button:hover .bottom {
    top: 0
}

.small-heading {
    text-transform: uppercase;
    letter-spacing: .1em;
    margin: 0 0 8px;
    font-size: 11px;
    font-weight: bold;
    color: rgba(0, 0, 0, 0.8)
}

.heading-border-b {
    color: rgba(0, 0, 0, 0.8);
    font-weight: bold;
    font-size: 16px;
    letter-spacing: 0.5px;
    border-bottom: 1px solid rgba(0, 0, 0, 0.1);
    padding-bottom: 0.5em
}

.heading-border-b a {
    color: #00ab6b
}

.border-top {
    border-top: 1px solid rgba(0, 0, 0, 0.1);
    padding-top: 1.5em
}

.center {
    text-align: center
}

.grayed-heading {
    color: rgba(0, 0, 0, 0.44);
    font-weight: bold;
    font-size: 16px
}

body.posts.show .row {
